#current {
	$background: hsl(241, 33%, 25%);
	background: $background;

	div {
		// variables
		$maxWidth: 320px;
		$maxHeight: $maxWidth;

		// colors
		$neutral100: hsl(0, 0%, 96%);
		$neutral600: hsl(0, 0%, 10%);
		$brown300: hsl(61, 35%, 33%);
		$orange300: hsl(23, 82%, 35%);
		$orange400: hsl(23, 82%, 43%);
		$purple500: hsl(241, 33%, 33%);

		width: $maxWidth;
		height: $maxHeight;
		position: relative;

		// temporary styles
		border: 1px solid $neutral100;

		// pumpkin
		&::before {
			content: '';
			position: absolute;
			width: 65%;
			height: 65%;
			bottom: 0%;

			$pumpkin: 
			// pumpkin middle
			radial-gradient($orange400 70%, transparent 70%) 50% 100% / 25% 40%,
			
			// pumpkin right
			radial-gradient($orange300 70%, transparent 70%) 60% 100% / 25% 40%,
			radial-gradient($orange400 70%, transparent 70%) 65% 100% / 40% 40%,
			radial-gradient($orange300 70%, transparent 70%) 75% 100% / 40% 40%,
			radial-gradient($orange400 70%, transparent 70%) 80% 100% / 40% 40%,
			
			// pumpkin left
			radial-gradient($orange300 70%, transparent 70%) 40% 100% / 25% 40%,
			radial-gradient($orange400 70%, transparent 70%) 35% 100% / 40% 40%,
			radial-gradient($orange300 70%, transparent 70%) 25% 100% / 40% 40%,
			radial-gradient($orange400 70%, transparent 70%) 20% 100% / 40% 40%,
	
			// pumpkin stem
			radial-gradient($background 70%, transparent 70%) 80% 55% / 28% 28%,
			radial-gradient($background 70%, transparent 70%) 20% 55% / 28% 28%,
			linear-gradient($brown300, $brown300) 50% 59% / 20% 15%;
	
			background: $pumpkin;
			background-repeat: no-repeat;
		}

